import * as FormItems from './formItem';
export default function aitiCheatFormGroup(path) {
  let group = [];
  switch (path) {
    case '/antiCheat':
      group = [FormItems.examCodeFormItem, FormItems.examTimeFormItem];
      break;
    case '/spotEwBlack':
      group = [FormItems.ewNameFormItem, FormItems.statusTypeFormItem];
      break;
    default:
      break;
  }
  return group;
}
